import { FunctionalComponent, h } from 'preact';
import style from './style.css';
import { checkinService, Store1, ICheckin } from "../../components/tracking/checkinService";
import { useCallback, useEffect, useState } from 'preact/hooks';

const Home: FunctionalComponent = () => {

    const db = new Store1();
    const [checkins, setCheckins] = useState([] as ICheckin[]);
    const [revision, setRevision] = useState(0);

    async function doCheckin(){
        await db.checkins.add({ in: new Date()})
        setRevision(x => x + 1)
    }

    async function doCheckout(){
        const id = getOpenCheckin(checkins)
        await db.checkins.update(id, {out: new Date()})
        setRevision(x => x + 1)
    }

    
    function getOpenCheckin(xs: ICheckin[]){
        const last = xs.slice(-1)
        if(last.length && !last[0].out){
            return last[0].id;
        }
    }

    useEffect(() => {
        async function loadData(){
            const results = await db.checkins.toArray()
            setCheckins(results);
        }
        loadData();
    }, [revision])
    
    const action = getOpenCheckin(checkins)
        ? <button onClick={doCheckout}>Checkout</button>
        : <button onClick={doCheckin}>Checkin</button>


    return (
        <div class={style.home}>
            <h1>Home</h1>
            <p>This is the Home component.</p>

            {action}

            {checkins.map(element => (
                <p>{element.id} {element.in.toString()} {element.out?.toString()}</p>
            ))}
        </div>
    );
};

export default Home;
