import Dexie from "dexie";

function CreateUUID() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
      var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
      return v.toString(16);
    });
}

export class Store1 extends Dexie {
    // Declare implicit table properties.
    // (just to inform Typescript. Instanciated by Dexie in stores() method)
    checkins: Dexie.Table<ICheckin>; // number = type of the primkey
    //...other tables goes here...

    constructor () {
        super("store1");
        this.version(1).stores({
            checkin: '++id, in, out, place',
            //...other tables goes here...
        });
        // The following line is needed if your typescript
        // is compiled using babel instead of tsc:
        this.checkins = this.table("checkin");
    }
}

export interface ICheckin {
    id?: number,
    in: Date,
    out?: Date,
    place?: string
}

export async function checkinService(){
    const db = new Store1();
    await db.transaction('rw', [db.checkins], async() => {
        
        if (await db.checkins.count() == 0){
            console.log("insert");
            await db.checkins.add({
                in: new Date(2021, 2, 23, 10, 30),
                out: new Date(2021, 2, 23, 12, 30),
            })
        }
        
    })

    return {
        get: async() => db.transaction('r', [db.checkins], () => db.checkins.toArray())
    }
    
}